<template>
  <div class="update-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      left-arrow
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <div class="field-wrap">
      <van-field
        v-model="nickName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateName',
  props: {
    // name: {
    //   type: String,
    //   required: true
    // }
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      nickName: this.value
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidclick: true // 
      })
      try {
        // 
        await updateUserProfile({
          name: this.nickName.trim() // 
        })

        // -
        // this.$emit('update-name', this.nickName)
        this.$emit('input', this.nickName)
        // 
        this.$emit('close')

        // 
        this.$toast.success('')
      } catch (err) {
        console.dir(err)
        if (err && err.response && err.response.status === 409) {
          this.$toast.fail(',!!!')
        } else if (err && err.response && err.response.status === 400) {
          this.$toast.fail('17!!!')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.field-wrap {
  padding: 10px;
}
</style>
