<template>
  <div class="update-birthday">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      minDate: new Date(1949, 9, 1),
      maxDate: new Date(2020, 11, 31),
      currentDate: new Date()
    }
  },
  methods: {
    async onConfirm () {
      // totast 
      this.$toast.loading({
        message: '',
        forbidclick: true // 
      })
      //   
      const birthdayData = dayjs(this.currentDate).format('YYYY-MM-DD')
      // 
      await updateUserProfile({
        birthday: birthdayData // '2018-12-20'
      })
      // -
      this.$emit('input', birthdayData)
      // 
      this.$emit('close')

      // 
      this.$toast.success('')
    }
  }
}
</script>

