<template>
  <div class="update-gender">
    <van-picker
      title=""
      show-toolbar
      :columns="columns"
      :default-index="defaultIndex"
      @confirm="onConfirm"
      @cancel="$emit('close')"
      @change="onGenderChange"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''], // 
      defaultIndex: this.value // 
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidclick: true // 
      })
      // 
      await updateUserProfile({
        gender: this.defaultIndex // 0-1-
      })

      // -
      this.$emit('input', this.defaultIndex)
      // 
      this.$emit('close')

      // 
      this.$toast.success('')
    },
    onGenderChange (picker, item, index) {
      this.defaultIndex = index
    }
  }
}
</script>

